import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [
    {
        path: '/',
        component: () => import('../views/tabs-header'),
        children: [
            {
                path: 'report/index',
                name: 'tabs-global',
                component: () => import('../views/tabs-global'),
                meta: {
                    title: '数据报告-全局报告',
                },
            },
            {
                path: 'report/yipai',
                name: 'tabs-yipai',
                component: () => import('../views/tabs-yipai'),
                meta: {
                    title: '数据报告-易湃',
                },
            },
            {
                path: 'report/ycwap',
                name: 'tabs-ycwap',
                component: () => import('../views/tabs-ycwap'),
                meta: {
                    title: '数据报告-易车网',
                },
            },
            {
                path: 'report/ycapp',
                name: 'tabs-ycapp',
                component: () => import('../views/tabs-ycapp'),
                meta: {
                    title: '数据报告-易车app',
                },
            },
            {
                path: 'report/qiche',
                name: 'tabs-qichebaojia',
                component: () => import('../views/tabs-qichebaojia'),
                meta: {
                    title: '数据报告-汽车报价大全',
                },
            },
            {
                path: 'report/operation',
                name: 'tabs-operation',
                component: () => import('../views/tabs-operation'),
                meta: {
                    title: '数据报告-运营',
                },
            },
        ],
    },
    {
        path: '/report/detail',
        name: 'detail',
        component: () => import('../views/detail'),
        meta: {
            title: '详情',
        },
    },
    {
        path: '*',
        redirect: '/report/index',
    }
]

export default new Router({
	mode: 'hash',
    linkActiveClass: '',
    linkExactActiveClass: 'bt-item-active',
	routes: [
		...routes
	]
})
